@use 'base/mixins' as m;
@use 'base/variables' as v;

.seccion-inferior{
    @include m.tablet{
        display: grid;
        grid-template-columns: 2fr 1fr;
        column-gap: 2rem;
    }
}
.entrada-blog{
    @include m.tablet{
        display: grid;
        grid-template-columns: 1fr 2fr;  
        column-gap: 2rem;
    }
    &:last-of-type{
        margin-bottom: 0;
    }

}
.entrada-texto{
    h4{ 
        margin-top:1rem;
        margin: 0;
        line-height: 1.4;
        &::after{
            content: '';
            display: block;
            width: 15rem;
            height: .5rem;
            background-color: v.$verde;
            margin-top: 1rem;
        }
    }
    a{
        color: v.$negro;
    }
    
}
.info-meta{
    span{
        color: v.$amarillo;
    }

}