@use 'base/variables' as v;

.testimonial{
    background-color:darken(v.$verde,15) ;
    font-size: 2.4rem;
    padding: 2rem;
    color:v.$blanco;
    position: relative;
    border-radius: 2rem;

    blockquote{
        padding-left: 5rem;
        &::before{
            content: '';
            background-image: url('../img/comilla.svg');
            background-repeat: no-repeat;
            width: 6rem;
            height: 8rem;
            display: block;
            position: absolute;
            left:0;
        }

    }
    p{
        color:v.$blanco;
        text-align: right;
    }
}