@use 'base/variables' as v;
@use 'base/mixins' as m;

.no-webp .inicio{
    background-image: url(../img/header.jpg);
}
.webp .inicio{
    background-image: url(../img/header.webp);
}
.header{
    background-color: v.$grisOscuro;
    padding: 1rem 0 3rem 0;
    &.inicio{
        background-position: center center;
        background-size: cover;

        @include m.tablet{
            min-height: 50rem;

        }
        .contenido-header{
            @include m.tablet{
                min-height: 50rem;
            }
        }
    }
}
.contenido-header{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    
    h1{
        text-align: left;
        color: v.$blanco;
        font-weight: 700;
        max-width: 30rem;
        font-size: 2.5rem;
        @include m.tablet{
            font-size: 3.8rem;
            max-width: 60rem;
        }
    }
}