@use 'base/variables' as v;

.admin-h1{
    font-weight: 500;
}
.admin{
    h2{
        font-weight: 300;
    }
}
.alerta{
    padding: .5rem;
    font-weight: bold;
    color: v.$blanco;
    text-align: center;
    margin-bottom: 1rem;
    text-transform: uppercase;
    &:first-of-type{
        margin-top: 1rem;
    }
    &.error{
        background-color: v.$rojo;
    }
    &.exito{
        background-color: darken($color: v.$verde, $amount: 15);
    }
}
table.propiedad{
    margin-top: 4rem;
    width: 100%;
    border-spacing: 0;
    thead{
        background-color: darken($color: v.$verde, $amount: 15) ;
        th{
            color: v.$blanco;
            padding: 2rem;
        }
    }
    .imagen-tabla{
        width: 10rem;
    }
    tbody{
        td{
            text-align: center;
        }
        tr{
            border:v.$negro 2px solid;
            background-color: v.$gris;
            &:nth-child(odd){
                background-color: darken($color: v.$gris, $amount: 5);
            }
        }
    }
}
.imagen-actualizar{
    width: 20rem;
}