@use 'base/mixins' as m;
@use 'base/variables' as v;
.barra{
    display: flex;
    flex-direction: column;
    padding-top: 2rem;
    align-items: center;
    justify-content: center;

    @include m.tablet{  
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-end;
        .menu-mobil{
            display: none;
        }
        
    }
}
.navegacion{
    visibility: hidden;
    opacity: 0;
    height: 0;
    transition: visibility 0.5s ease-in-out,opacity 0.5s linear, height 0.5s linear;
    @include m.tablet{
        visibility: visible;
        opacity: 1;
        display: flex;
        align-items: center;
    }
    a{
        color:v.$blanco;
        text-decoration: none;
        font-size: 2.2rem;
        display: block;
        text-align: center;
        &:hover{
            color: v.$verde;
        }
        @include m.tablet{
            display:inline-block;
            margin-right: 2rem;
            align-content:center;
            &:last-of-type{
                margin-right: 0;
            }
        }
    }
}
.menu-mobil{
    width: 4rem;
    cursor: pointer;
    @include m.tablet{
        display: none;
    }
}
.mostrar{
    visibility: visible;
    opacity: 1;
    height: auto;
}
.barra-derecha{
    display:flex;
    flex-direction: column;
    align-items: center;

    @include m.tablet{
        align-items:flex-end;
               
    }
    .boton-dark-mode{
        width:3rem;
        margin: 3rem 0;
        filter: invert(100%);
        &:hover{
            cursor: pointer;
        }
        @include m.tablet{
            margin-bottom: 2rem;
            margin-top:0;
            
        }

    }
}