@use 'base/variables' as v;


.form{

    p{
        font-size:1.5rem;
        color: v.$grisOscuro;
        margin:2rem 0 0 0;
    }
    legend{
        font-weight:bold;
        text-transform:uppercase;
    }
    label{
        display: block;
        font-weight: 500;
    }
    input:not([type='submit']),textarea,
    select{
        padding:1rem;
        display: block;
        width: 100%;
        background-color: v.$blanco;
        border: 1px solid v.$gris;
        border-radius: 1rem;
        margin-bottom: 2rem;
    }
   input[type="radio"]{
    accent-color: v.$verde;
   }
    textarea{
        height: 20rem;
        resize: none;
    }

}
.forma-contacto{

    width: 30rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    input[type='radio']{
        margin: 0;
        width: auto;
    }
}