.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.w-100 {
  width: 100%;
}

.boton-amarillo-block {
  background-color: #E08709;
  color: #fafafa;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.boton-amarillo-block:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton-amarillo {
  background-color: #E08709;
  color: #fafafa;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.boton-amarillo:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton-verde {
  background-color: #399c3c;
  color: #fafafa;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.boton-verde:hover {
  background-color: #328934;
  cursor: pointer;
}

.boton-verde-block {
  background-color: #399c3c;
  color: #fafafa;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.boton-verde-block:hover {
  background-color: #328934;
  cursor: pointer;
}

.boton-rojo-block {
  background-color: rgba(224, 19, 19, 0.8470588235);
  color: #fafafa;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.boton-rojo-block:hover {
  background-color: rgba(200, 17, 17, 0.8470588235);
  cursor: pointer;
}

.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode p {
  color: #fafafa;
}
.dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4, .dark-mode blockquote, .dark-mode td {
  color: #e1e1e1;
}
.dark-mode .precio {
  color: #5dc460;
}
.dark-mode .anuncio {
  background-color: #333333;
}
.dark-mode .anuncio .icono {
  filter: invert(90%);
}
.dark-mode .resumen-propiedad .icono {
  filter: invert(90%);
}
.dark-mode .form label, .dark-mode .form legend, .dark-mode .form p {
  color: #e1e1e1;
}
.dark-mode .form input:not([type=submit]), .dark-mode .form textarea,
.dark-mode .form select {
  background-color: #333333;
  color: #fafafa;
  border: none;
  background-image: #fafafa;
}
.dark-mode .form input[type=date]::-webkit-calendar-picker-indicator,
.dark-mode .form input[type=time]::-webkit-calendar-picker-indicator {
  filter: invert(100%);
}
.dark-mode table.propiedad tbody tr {
  background-color: #1a1a1a;
}
.dark-mode table.propiedad tbody tr:nth-child(odd) {
  background-color: #1a1a1a;
}

@media (min-width: 768px) {
  .contenido-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.texto-nosotros blockquote {
  font-weight: 900;
  font-size: 2.2rem;
  margin: 2rem 0 0 0;
  padding: 1rem 0 3rem 0;
}

.admin-h1 {
  font-weight: 500;
}

.admin h2 {
  font-weight: 300;
}

.alerta {
  padding: 0.5rem;
  font-weight: bold;
  color: #fafafa;
  text-align: center;
  margin-bottom: 1rem;
  text-transform: uppercase;
}
.alerta:first-of-type {
  margin-top: 1rem;
}
.alerta.error {
  background-color: rgba(224, 19, 19, 0.8470588235);
}
.alerta.exito {
  background-color: #399c3c;
}

table.propiedad {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
table.propiedad thead {
  background-color: #399c3c;
}
table.propiedad thead th {
  color: #fafafa;
  padding: 2rem;
}
table.propiedad .imagen-tabla {
  width: 10rem;
}
table.propiedad tbody td {
  text-align: center;
}
table.propiedad tbody tr {
  border: #000000 2px solid;
  background-color: #e1e1e1;
}
table.propiedad tbody tr:nth-child(odd) {
  background-color: #d4d4d4;
}

.imagen-actualizar {
  width: 20rem;
}

.no-webp .inicio {
  background-image: url(../img/header.jpg);
}

.webp .inicio {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 50rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 50rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  text-align: left;
  color: #fafafa;
  font-weight: 700;
  max-width: 30rem;
  font-size: 2.5rem;
}
@media (min-width: 768px) {
  .contenido-header h1 {
    font-size: 3.8rem;
    max-width: 60rem;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  padding-top: 2rem;
  align-items: center;
  justify-content: center;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
  .barra .menu-mobil {
    display: none;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.5s ease-in-out, opacity 0.5s linear, height 0.5s linear;
}
@media (min-width: 768px) {
  .navegacion {
    visibility: visible;
    opacity: 1;
    display: flex;
    align-items: center;
  }
}
.navegacion a {
  color: #fafafa;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
.navegacion a:hover {
  color: #5dc460;
}
@media (min-width: 768px) {
  .navegacion a {
    display: inline-block;
    margin-right: 2rem;
    align-content: center;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}

.menu-mobil {
  width: 4rem;
  cursor: pointer;
}
@media (min-width: 768px) {
  .menu-mobil {
    display: none;
  }
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.barra-derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .barra-derecha {
    align-items: flex-end;
  }
}
.barra-derecha .boton-dark-mode {
  width: 3rem;
  margin: 3rem 0;
  filter: invert(100%);
}
.barra-derecha .boton-dark-mode:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .barra-derecha .boton-dark-mode {
    margin-bottom: 2rem;
    margin-top: 0;
  }
}

.footer {
  padding: 2.5rem 0;
  background-color: #333333;
  margin-bottom: 0;
}
.footer .contenedor-footer {
  padding-top: 2.5rem;
}
.footer .copyright {
  color: #fafafa;
  text-align: center;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.mas-nosotros {
  font-weight: 400;
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.iconos-nosotros .icono img {
  height: 10rem;
}
.iconos-nosotros .icono p {
  text-align: center;
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.anuncio {
  background-color: #e6dcfa;
  border: 1px solid #c8c8c8;
  margin-bottom: 1rem;
}
.anuncio .contenido-anuncio {
  padding: 2rem;
}
.anuncio .contenido-anuncio h3, .anuncio .contenido-anuncio p {
  margin: 0;
}

.precio {
  color: #399c3c;
  font-weight: bold;
  font-size: 3rem;
}

.iconos-caracteristicas {
  display: flex;
  list-style: none;
  padding: 0;
  max-width: 40rem;
}
.iconos-caracteristicas li {
  flex: 1;
  display: flex;
}
.iconos-caracteristicas li img {
  flex: 0 0 3rem;
  margin-right: 1rem;
}
.iconos-caracteristicas li p {
  font-weight: 700;
}

.titulo-propiedad, .descripcion-propiedad {
  height: 20vh;
  overflow: hidden;
}

.no-webp .imagen-contacto {
  background-image: url("../img/encuentra.jpg");
}

.webp .imagen-contacto {
  background-image: url("../img/encuentra.webp");
}

.imagen-contacto {
  background-position: center center;
  background-size: cover;
  padding: 5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.imagen-contacto h2 {
  color: #fafafa;
  font-weight: 700;
}
.imagen-contacto p {
  color: #fafafa;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}

@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}

.entrada-texto h4 {
  margin-top: 1rem;
  margin: 0;
  line-height: 1.4;
}
.entrada-texto h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #5dc460;
  margin-top: 1rem;
}
.entrada-texto a {
  color: #000000;
}

.info-meta span {
  color: #E08709;
}

.testimonial {
  background-color: #399c3c;
  font-size: 2.4rem;
  padding: 2rem;
  color: #fafafa;
  position: relative;
  border-radius: 2rem;
}
.testimonial blockquote {
  padding-left: 5rem;
}
.testimonial blockquote::before {
  content: "";
  background-image: url("../img/comilla.svg");
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: 0;
}
.testimonial p {
  color: #fafafa;
  text-align: right;
}

.form p {
  font-size: 1.5rem;
  color: #333333;
  margin: 2rem 0 0 0;
}
.form legend {
  font-weight: bold;
  text-transform: uppercase;
}
.form label {
  display: block;
  font-weight: 500;
}
.form input:not([type=submit]), .form textarea,
.form select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #fafafa;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.form input[type=radio] {
  accent-color: #5dc460;
}
.form textarea {
  height: 20rem;
  resize: none;
}

.forma-contacto {
  width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.forma-contacto input[type=radio] {
  margin: 0;
  width: auto;
}