@use 'base/mixins' as m;
@use 'base/variables' as v;
.contenedor-anuncios{
    @include m.tablet{
        @include m.grid(3,2rem);
        
    }
}
.anuncio{
    background-color: lighten($color: v.$moradoClaro, $amount: 7);
    border: 1px solid darken($color: v.$gris, $amount: 10);
    margin-bottom: 1rem;
    .contenido-anuncio{
        padding: 2rem;

        h3,p{
            margin: 0;
        }

        
    }
}
.precio{
    color:  darken($color: v.$verde, $amount: 15) ;
    font-weight: bold;
    font-size: 3rem;
}
.iconos-caracteristicas{
    display: flex;
    list-style: none;
    padding: 0;
    max-width: 40rem;
    li{
        flex:1;
        display: flex;

        img{
            flex: 0 0 3rem;
            margin-right: 1rem;
        }
        p{
            font-weight: 700;
        }
    }

}
.titulo-propiedad,.descripcion-propiedad{
    height: 20vh;
    overflow: hidden;
}
